.header__section {
  position: fixed;
  top: 0px;
  left: 0;
  width: 100%;
  height: 6rem;
  z-index: 6;
  background: transparent;
  backdrop-filter: blur(4px);
  vertical-align: center;
  /* border: 1px solid white; */
  display: none;
}

ul {
  display: flex;
  /* border: 1px solid white; */
  gap: 3rem;
  margin-left: 1rem;
}

ul a {
  text-align: center;
  font-weight: bold;
  font-size: larger;
  cursor: pointer;
}

.source__code {
  margin-right: 1rem;
}

.source__code > a {
  border: 1px solid white;
  padding: 0.4rem 1rem;
  border-radius: 10px;
  text-align: right;
  float: right;
  font-weight: bolder;
  font-size: larger;
  background: #4db5ff;
  color: black;
  cursor: pointer;
}

.source__code > a:hover {
  background: white;
  color: black;
}

@media screen and (min-width: 860px) {
  .header__section {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
